import React from 'react';
import { number, string } from 'prop-types';

function LegendaryGemsComponent({name, rank, desc, mainEffect, secondaryEffect}) {
    return (
        <div id="diablo-item">
            <div className="d3-tooltip d3-tooltip-item">
                <div className="tooltip-head tooltip-head-orange" style={{textAlign: 'center'}}>
                    <h3 className="d3-color-orange ">{name}</h3>
                </div>
                <div className="tooltip-body">
                    <div className="d3-item-properties">
                        <div className="item-description d3-color-white">
                            <p className="d3-color-gray" style={{ float: 'right'}}>Rank {rank}</p>
                            <p className="d3-color-orange">Legendary Gem</p>
                        </div>
                        <ul className="item-effects">
                            <li className="d3-color-gray d3-item-property-default">
                                <p>
                                    Can be insterted into
                                    <span className="property-0"> jewelry </span>
                                    with sockets.
                                </p>
                            </li>
                        </ul>
                        <div className="item-before-effects"/>
                        <ul className="item-effects">
                            <li className="d3-color-orange d3-item-property-default d3-item-property-utility">
                                <p className="property-1" dangerouslySetInnerHTML={{__html: mainEffect}}></p>
                            </li>
                            <li className={"level25 d3-color-gray d3-item-property-default d3-item-property-utility " + ((rank >= 25) ? "d3-color-orange" : "" )}>
                                <p className="property-2">{secondaryEffect}{ (rank < 25) ? <span className="required d3-color-red"> (Requires Rank 25)</span> : null}</p>
                            </li>
                        </ul>
                    </div>
                    <div className="tooltip-extension">
                        <div className="flavor">
                            <p className="property-3">{desc}</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

LegendaryGemsComponent.defaultProps = {};

LegendaryGemsComponent.propTypes = {
    name: string,
    rank: number,
    desc: string,
    mainEffect: string,
    secondaryEffect: string
}

export default LegendaryGemsComponent;
