
import React, { useState, useEffect} from 'react';
import { Container, FormGroup, FormCheck, Button} from 'react-bootstrap';
import legendaryGems from '../../resources/legendaryGems';
import LegendaryGemsComponent from '../../components/site/LegendaryGemsComponent'

import BaneOfThePowerful from '../../assets/image/legendary-gems/1.png';
import BaneOfTheTrapped from '../../assets/image/legendary-gems/2.png';
import Enforcer from '../../assets/image/legendary-gems/3.png';
import GemOfEfficaciousToxin from '../../assets/image/legendary-gems/4.png';
import GogokOfSwiftness from '../../assets/image/legendary-gems/5.png';
import InvigoratingGemstone from '../../assets/image/legendary-gems/6.png';
import Mirinae_TeardropOfTheStarweaver from '../../assets/image/legendary-gems/7.png';
import Moratorium from '../../assets/image/legendary-gems/8.png';
import PainEnhancer from '../../assets/image/legendary-gems/9.png';
import SimplicitysStrength from '../../assets/image/legendary-gems/10.png';
import Taeguk from '../../assets/image/legendary-gems/11.png';
import WreathOfLightning from '../../assets/image/legendary-gems/12.png';
import ZeisStoneOfVengeance from '../../assets/image/legendary-gems/13.png';
import BoonOfTheHoarder from '../../assets/image/legendary-gems/14.png';
import EsotericAlteration from '../../assets/image/legendary-gems/15.png';
import GemOfEase from '../../assets/image/legendary-gems/16.png';
import MoltenWildebeestsGizzard from '../../assets/image/legendary-gems/17.png';
import Iceblink from '../../assets/image/legendary-gems/18.png';
import MutilationGuard from '../../assets/image/legendary-gems/19.png';
import BaneOfTheStricken from '../../assets/image/legendary-gems/20.png';
import BoyarskysChip from '../../assets/image/legendary-gems/21.png';
import RedSoulstoneShard from '../../assets/image/legendary-gems/22.png';
import LegacyOfDreams from '../../assets/image/legendary-gems/23.png';
import { element } from 'prop-types';

function GemCalculatorComponent() {

    const [firstLoad, setFirstLoad] = useState(true);
    const [tempGemRank, setTempGemRank] = useState(0);
    const [selectedGemId, setSelectedGemId] = useState("");
    const [selectedGemName, setSelectedGemName] = useState("");
    const [selectedGemRank, setSelectedGemRank] = useState(0);
    const [selectedGemMainEffect, setSelectedGemMainEffect] = useState("");
    const [selectedGemSecondaryEffect, setSelectedGemSecondaryEffect] = useState("");
    const [selectedGemDesc, setSelectedGemDesc] = useState("");

    const [riftLevel, setRiftLevel] = useState(1);

    const [ probability, setProbability] = useState({
        winSome: {
            probabilityValue: "0.0%",
            bkColor: "hsl(0, 40%, 50%)"
        },
        winAll: {
            probabilityValue: "0.0%",
            bkColor: "hsl(0, 40%, 50%)"
        },
        failAll: {
            probabilityValue: "0.0%",
            bkColor: "hsl(0, 40%, 50%)"
        }
    });

    const setSelectedGemToShow = (id, gemRank, riftLevel, empowered, freeRank, upgrade) => {

        var level = 12;
        var rankJump = 3;
        var tempRiftLevel = riftLevel;

        for(let i = 0; i < legendaryGems.gemsList.length; i++) {

            if (id == legendaryGems.gemsList[i].id) {
                
                let tempMainEffect = legendaryGems.gemsList[i].engMainEffect;

                for (let j = 0; j < legendaryGems.gemsList[i].basePower.length; j++) {
                    let replacementString = "<span className=\"d3-color-blue\"><span className=\"value\">" +
                                            ((id == "16") ? "+" : "") + 
                                            (   legendaryGems.gemsList[i].basePower[j] + 
                                                ((gemRank != 0) ? legendaryGems.gemsList[i].eachLvlPower[j] * gemRank : 0)    ) + 
                                            ((legendaryGems.gemsList[i].percentTag) ? '%' : '') + 
                                            "</span></span>";
                    tempMainEffect = tempMainEffect.replace("{{value}}", replacementString);
                }
                
                setSelectedGemId(id);
                setSelectedGemName(legendaryGems.gemsList[i].engName);
                setSelectedGemRank(gemRank);
                setSelectedGemMainEffect(tempMainEffect);
                setSelectedGemSecondaryEffect(legendaryGems.gemsList[i].engSecondaryEffect);
                setSelectedGemDesc(legendaryGems.gemsList[i].engDescription);
                
                break;
            }
        }

        if (freeRank) {
            level++;
            rankJump++
        }

        if (empowered) {
            level++;
            rankJump++;
        }

        if (upgrade) {
            tempRiftLevel = parseFloat(gemRank) + parseFloat(level);
            setRiftLevel(tempRiftLevel);
        }

        var pct0 = loadChance(tempRiftLevel,parseFloat(gemRank)+0);
        var pct1 = loadChance(tempRiftLevel,parseFloat(gemRank)+1);
        var pct2 = loadChance(tempRiftLevel,parseFloat(gemRank)+2);
        var pct3 = loadChance(tempRiftLevel,parseFloat(gemRank)+3);
        var pct4 = loadChance(tempRiftLevel,parseFloat(gemRank)+4);

        var FFF = 100 * (1-(.01*pct0)) * (1-(.01*pct0)) * (1-(.01*pct0));
        var SSS = 100 * (.01*pct0) * (.01*pct1) * (.01*pct2);

        if (empowered && freeRank) {
            FFF *= (1-(.01*pct0));
            FFF *= (1-(.01*pct0));
            SSS *= (.01*pct3);
            SSS *= (.01*pct4);
        } else if (freeRank || empowered) {
            FFF *= (1-(.01*pct0));
            SSS *= (.01*pct3);
        }

        var GT0 = 100 * (1-FFF*.01);

        setProbability((prevState) => ({
            ...prevState,
            winSome: {
                ...prevState.winSome,
                probabilityValue: GT0.toFixed(1)+'%',
                bkColor: 'hsl(' + (GT0/100)*200 + ', 40%, 50%)'
            },
            winAll: {
                ...prevState.winAll,
                probabilityValue: SSS.toFixed(1)+'%',
                bkColor: 'hsl(' + (SSS/100)*200 + ', 40%, 50%)'
            },
            failAll: {
                ...prevState.failAll,
                probabilityValue: FFF.toFixed(1)+'%',
                bkColor: 'hsl(' + (FFF/100)*200 + ', 40%, 50%)'
            }
        }))

    }

    const loadChance = (grlvl, gem) => {
        var result = grlvl - gem;
        if(result >= 10) {return 100;}
        else if(result >=  9) {return 90;}
        else if(result >=  8) {return 80;}
        else if(result >=  7) {return 70;}
        else if(result >=  0) {return 60;}
        else if(result >= -1) {return 30;}
        else if(result >= -2) {return 15;}
        else if(result >= -3) {return 8;}
        else if(result >= -4) {return 4;}
        else if(result >= -5) {return 2;}
        else if(result >= -15) {return 1;}
        else {return 0};
    }

    const calculateLegendaryGem = () => {
        let empowered = document.getElementById("empowered").checked;
        let freeRank = document.getElementById("free-rank").checked;
        let upgrade = document.getElementById("upgrade").checked;
        setSelectedGemToShow(selectedGemId, tempGemRank, riftLevel, empowered, freeRank, upgrade);
    }

    const resetCalculateLegendaryGem = () => {
        document.getElementById("empowered").checked = false;
        document.getElementById("free-rank").checked = false;
        document.getElementById("upgrade").checked = false;
        setSelectedGemToShow("1", 0, riftLevel, false, false, false);
    }

    const gemImgPathMap = {
        "1": BaneOfThePowerful,
        "2": BaneOfTheTrapped,
        "3": Enforcer,
        "4": GemOfEfficaciousToxin,
        "5": GogokOfSwiftness,
        "6": InvigoratingGemstone,
        "7": Mirinae_TeardropOfTheStarweaver,
        "8": Moratorium,
        "9": PainEnhancer,
        "10": SimplicitysStrength,
        "11": Taeguk,
        "12": WreathOfLightning,
        "13": ZeisStoneOfVengeance,
        "14": BoonOfTheHoarder,
        "15": EsotericAlteration,
        "16": GemOfEase,
        "17": MoltenWildebeestsGizzard,
        "18": Iceblink,
        "19": MutilationGuard,
        "20": BaneOfTheStricken,
        "21": BoyarskysChip,
        "22": RedSoulstoneShard,
        "23": LegacyOfDreams,
    }

    const getGemImage = (id) => {
        let obj = null;
        Object.entries(gemImgPathMap).forEach(([key, value]) => {
            if (key == id) {
                obj = value;
                return obj;
            }
        });
        return obj
    }

    useEffect(() => {
        if (firstLoad) {
            setSelectedGemToShow("1", 0, riftLevel, false, false, false);
            setFirstLoad(!firstLoad);
        }
    });
    
    return (
        <Container fluid="lg">
            <div className="row border border-dark rounded">
                <div className="col-md-12" style={{ marginTop: '10px' }}>
                    <p className="fw-bold">CHOOSE A GEM</p>
                </div>  
                <div className="d-flex justify-content-center">
                    <div className="col-md-12" style={{ width: '95%', flexWrap: 'wrap', display: 'flex'}}>
                        {
                            legendaryGems.gemsList.map((element, index) => (
                                <div className="p-1" key={index} style={{marginBottom: '10px', width: '100px', height: '100px'}}>
                                    <img className={"gems " + ((selectedGemId == element.id) ? "clicked" : "")  + " mx-auto d-block center-block"} title={element.engName} src={getGemImage(element.id)}
                                            onClick={() => setSelectedGemId(element.id)} style={{marginTop: '20px'}}/>
                                </div>
                            ))
                        }
                    </div>
                </div>
            </div>
            <div className="row border border-dark rounded" style={{ marginTop: '10px' }}>
                <div className="col-md-12 d-flex justify-content-center">
                    <div className="p-1 align-self-center" style={{width: '354px !important'}}>
                        {/* legendary gems component */}
                        <LegendaryGemsComponent name={selectedGemName} rank={selectedGemRank} desc={selectedGemDesc} mainEffect={selectedGemMainEffect} secondaryEffect={selectedGemSecondaryEffect}/>
                    </div>
                    <div className="p-5 align-self-center" style={{ marginLeft: 'px'}} style={{width: '40%'}}>
                        <div className="row">
                            <div className="col-md-12">
                                <FormGroup>
                                    <p className="fw-bold" >
                                        Gem Rank
                                    </p>
                                    <input id='gemRank' className="form-control" type='number' value={tempGemRank} 
                                            onChange={e => setTempGemRank(e.target.value)} />
                                </FormGroup>
                                <br/>
                                <FormGroup>
                                    <p className="fw-bold" >
                                        Rift Level
                                    </p>
                                    <input id='riftLevel' className="form-control" type='number' value={riftLevel} 
                                            onChange={e => setRiftLevel(e.target.value)} />
                                </FormGroup>
                                <br/>
                                <FormCheck>
                                    <input className="form-check-input" type="checkbox" id="empowered" />
                                    <label className="form-check-label" htmlFor="empowered">
                                        Empowered
                                    </label>
                                </FormCheck>
                                <br/>
                                <FormCheck>
                                    <input className="form-check-input" type="checkbox" id="free-rank" />
                                    <label className="form-check-label" htmlFor="free-rank">
                                        No-Death Bonus
                                    </label>
                                </FormCheck>
                                <br/>
                                <FormCheck>
                                    <input className="form-check-input" type="checkbox" id="upgrade" />
                                    <label className="form-check-label" htmlFor="upgrade">
                                        100% Upgrade
                                    </label>
                                </FormCheck>
                                <br/>
                                <Button id="calGemResetBtn" onClick={() => resetCalculateLegendaryGem()}>Reset</Button>
                                <Button id="calGemCalculateBtn" onClick={() => calculateLegendaryGem()}>
                                        Calculate
                                </Button>
                            </div>
                        </div>                      
                    </div>
                </div>
                <div className="col-md-12">
                    {/* <div className="row d-flex justify-content-center">
                        <div className="col-md-1" style={{width: 'auto'}}/>
                        <div className="col-md-2" style={{width: '15%'}}>
                            <p className="fw-bold">
                                Chances
                            </p>
                            <div className="border border-black rounded" style={{height: '45px', backgroundColor: '#B34D4D'}}> 
                            </div>
                            <div style={{textAlign: 'center', marginTop: '10px'}}>
                            </div>
                        </div>
                        <div className="col-md-2" style={{width: '15%'}}>
                            <div className="border border-black rounded" style={{height: '45px', backgroundColor: '#B34D4D', marginTop: '40px'}}>
                            </div>
                            <div style={{textAlign: 'center', marginTop: '10px'}}> 
                            </div>  
                        </div>
                        <div className="col-md-2" style={{width: '15%'}}>
                            <div className="border border-black rounded" style={{height: '45px', backgroundColor: '#B34D4D', marginTop: '40px'}}>
                            </div>
                            <div style={{textAlign: 'center', marginTop: '10px'}}> 
                            </div>
                        </div>
                        <div className="col-md-2" style={{width: '15%'}}>
                            <div className="border border-black rounded" style={{height: '45px', backgroundColor: '#B34D4D', marginTop: '40px'}}>
                            </div>
                            <div style={{textAlign: 'center', marginTop: '10px'}}> 
                            </div>
                        </div>
                        <div className="col-md-2"style={{width: '15%'}}>
                            <div className="border border-black rounded" style={{height: '45px', backgroundColor: '#B34D4D', marginTop: '40px'}}>
                            </div>
                            <div style={{textAlign: 'center', marginTop: '10px'}}> 
                            </div>
                        </div>
                        <div className="col-md-1" style={{width: 'auto'}}/>
                    </div> */}
                    <div className="row d-flex justify-content-center">
                        <div className="col-md-4" style={{width: '25%'}}>
                            <p className="fw-bold">
                                Probability
                            </p>
                            <div className="border border-black rounded " style={{height: '45px', backgroundColor: probability.winSome.bkColor}}>
                                <p className="fs-3" style={{marginTop: '2px', textAlign: 'center'}}>{probability.winSome.probabilityValue}</p>
                            </div>
                            <div style={{marginTop: '10px'}}>
                                <p className="fw-bold" style={{textAlign: 'center'}}>Win Some</p>
                            </div>
                        </div>
                        <div className="col-md-4" style={{width: '25%'}}>
                            <div className="border border-black rounded" style={{ height: '45px', backgroundColor: probability.winAll.bkColor, marginTop: '40px'}}>
                                <p className="fs-3" style={{marginTop: '2px', textAlign: 'center'}}>{probability.winAll.probabilityValue}</p>
                            </div>
                            <div style={{marginTop: '10px'}}>
                                <p className="fw-bold" style={{textAlign: 'center'}}>Win All</p>
                            </div>
                        </div>
                        <div className="col-md-4" style={{width: '25%'}}>
                            <div className="border border-black rounded" style={{height: '45px', backgroundColor: probability.failAll.bkColor, marginTop: '40px'}}>
                                <p className="fs-3" style={{marginTop: '2px', textAlign: 'center'}}>{probability.failAll.probabilityValue}</p>
                            </div>
                            <div style={{marginTop: '10px'}}>
                                <p className="fw-bold" style={{textAlign: 'center'}}>Fail All</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Container>
    );
}

export default GemCalculatorComponent;
